import hanger from './../../../assets/images/hanger.png';
import certificate from './../../../assets/images/certificate.png';
import payment from './../../../assets/images/payment.png';
import HeaderTopItem from './HeaderTopItem/HeaderTopItem';
import HeaderTopGeo from './HeaderTopGeo/HeaderTopGeo';
import styles from './HeaderTop.module.css';
import cn from 'classnames';

const HeaderTop = (props) => {
    let deliveryTipText =  ` ,     ,    ?
       .
        ,   ,       .`;
    let certifiedTipText = `  - .   .`;
    let paymentTipText = `Lamoda     : -     
     Lamoda        .`

    return (
        <div className={styles.header__top} >
          <div className={cn(styles.header__top_inner, styles.wrapper)}>
            <HeaderTopGeo />
            <HeaderTopItem link={'/delivery/'} image={hanger} description={'  '} alt={'hanger'} tipText={deliveryTipText} />
            <HeaderTopItem link={'/help/podlinnost-produkcii-kz/'} image={certificate} description={' '} alt={'certificate'} tipText={certifiedTipText} />
            <HeaderTopItem link={'/lp/postoplata'} image={payment} description={'  '} alt={'payment'} isRed tipText={paymentTipText} />
          </div>
        </div>
    )
}

export default HeaderTop;